@import '../../../styles/package/allPackage.scss';

.button {
	width: 100%;
	padding: 14px;
	text-align: center;
	border-radius: 8px;
	background-color: var(--primary-color);
	margin-top: 30px;

	&:hover {
		cursor: pointer;
		filter: brightness(0.8);
		transition: all 300ms ease;
	}

	> p {
		font-weight: bold;
		color: var(--white-color);
	}
}
